.title-first {
  color: $color-white;
  &__main {
    display: block;
    letter-spacing: 1rem;
    line-height: 6rem;
    font-size: 5rem;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 1rem;
    animation-name: leftToRight;
    animation-duration: 1.7s;
    animation-delay: 1s;
    animation-timing-function: ease;
    backface-visibility: hidden; }
  &__secondary {
    display: block;
    line-height: 4.8rem;
    font-size: 4rem;
    font-weight: 100;
    letter-spacing: 0.5rem;
    animation-name: RightToLeft;
    animation-duration: 1.7s;
    animation-delay: 1s;
    animation-timing-function: ease;
    backface-visibility: hidden;
    margin-bottom: 6.5rem; } }

body {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: $color-gray;
  font-size: 1.6rem;
  letter-spacing: .2rem; }
